<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process201"></a>

<p><br></p>
<p><span class="rvts17">受宠竞赛（</span><span class="rvts50">Popularity Contest</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:184,Y:674</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：棉花尾巴（</span><span class="rvts20">Cottontail</span><span class="rvts21">），点点（</span><span class="rvts20">Dot</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">选出猫和狗谁更可爱</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1247.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">在瑞克的卧室，可以看到一只叫做棉花尾巴（</span><span class="rvts20">Cottontail</span><span class="rvts21">）的猫和一只叫做点点（</span><span class="rvts20">Dot</span><span class="rvts21">）的狗。他们两个因为争宠而在大吵大叫。</span>
<br><span class="rvts21">使用</span><span class="rvts20"></span><span class="rvts21">宠物伙伴</span><span class="rvts20"></span><span class="rvts21">天赋与它们交谈，并选择谁更可爱，将会获得额外的经验值并关闭该支线。</span>
<br><span class="rvts21">如果选择猫更可爱，它会告诉你旁边睡床地板上有一颗隐藏的大钻石；如果选择狗更可爱，它会告诉你楼下有一个隐藏的地下室，通往老鼠和乌龟的地方（支线：异性相吸）。</span>
<br><span class="rvts21">如果选择它们两个都可爱，那么它们两个会感觉你太过于阿谀奉承，不会告诉你瑞克洋馆的任何隐藏秘密。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return {};
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



